html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5, {
  margin: 0;
  padding: 0;
  border-width: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: inherit;
}

body {
  line-height: 1.5;
}

strong {
  font-weight: 500;
}

p {
  line-height: 27px;
  color: #3e3e3e;
}
